@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap");
@import '../../node_modules/swiper/swiper.min.css';
*,
*::before,
*::after {
  box-sizing: border-box;
}

@media (prefers-reduced-motion: no-preference) {
  :root {
    scroll-behavior: smooth;
  }
}
.visually-hidden-focusable:not(:focus):not(:focus-within) {
  clip: rect(0, 0, 0, 0) !important;
  border: 0 !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

body {
  margin: 0;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  letter-spacing: 0.2px;
}

p {
  color: #737373;
  font-weight: 500;
  line-height: 1.42;
}

small {
  font-size: 12px;
  color: #BDBDBD;
  line-height: 2.3;
}

.container {
  max-width: 1134px;
  margin: 0 auto;
  padding: 0 42px;
}

.btn {
  padding: 10px 36px;
  color: #FFFFFF;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 2;
  letter-spacing: 0.2px;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  border: none;
}
.btn:hover {
  opacity: 0.8;
}
.btn.btn-primary {
  background-color: #FFA62B;
  border-radius: 5px;
  padding: 15px 40px;
}
.btn.btn-secondary {
  background-color: #00A0C1;
  border-radius: 37px;
}
.btn.btn-ghost {
  background-color: transparent;
  border-radius: 37px;
  border: 1px solid #00A0C1;
  color: #00A0C1;
}
.btn:disabled {
  background-color: #BDBDBD;
  cursor: not-allowed;
}
.btn.loading {
  position: relative;
}
.btn.loading:before {
  content: "";
  position: absolute;
  z-index: 5;
  top: 30%;
  right: 20px;
  width: 24px;
  height: 24px;
  border: 4px solid #FFFFFF;
  border-bottom-color: #FFA62B;
  border-radius: 50%;
  display: inline-block;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
  background-color: transparent;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
label {
  display: block;
}
label span {
  display: inline-block;
  margin-bottom: 10px;
  color: #252B42;
  font-weight: 600;
  line-height: 1.7;
  cursor: pointer;
}

input[type=text],
input[type=email],
select,
textarea {
  border-radius: 5px;
  border: 1px solid #BDBDBD;
  background: #F9F9F9;
  padding: 10px 20px;
  color: #737373;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  width: 100%;
  line-height: 2;
  margin-bottom: 7px;
  height: 50px;
}

textarea {
  height: 140px;
}

.error-msg {
  color: red;
}

.success-msg {
  color: #14ae5c;
}

.form-msg {
  text-align: center;
  margin-top: 16px;
}

.section-title {
  text-align: center;
  max-width: 470px;
  margin: 0 auto;
}

.section-desc {
  text-align: center;
  margin: 10px auto 0;
  max-width: 470px;
}

.link {
  color: #FFA62B;
  font-weight: 600;
  line-height: 1.7;
  text-decoration: none;
}

h6, .h6, h5, .h5, h4, .h4, h3, .h3, h2, .h2, h1, .h1 {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  line-height: 1.42;
  color: #252B42;
}

h1, .h1 {
  font-size: 58px;
}

h2, .h2 {
  font-size: 40px;
}

h3, .h3 {
  font-size: 24px;
}

h4, .h4 {
  font-size: 20px;
}

h5, .h5 {
  font-size: 16px;
}

h6, .h6 {
  font-size: 14px;
}

#header {
  height: 104px;
  padding: 23px 59px;
  position: absolute;
  z-index: 100;
  width: 100%;
}
#header .sprivaten-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#header .sprivaten-nav .sprivaten-menu-nav {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 0;
  grid-gap: 21px;
}
#header .sprivaten-nav .sprivaten-menu-nav a {
  color: #737373;
  text-align: center;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.7;
  letter-spacing: 0.2px;
  text-decoration: none;
}
#header .sprivaten-nav .sprivaten-menu-nav a:hover {
  opacity: 0.8;
}

#footer {
  background-color: #252B42;
}
#footer .footer-brand {
  padding: 40px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#footer .footer-brand .footer-socials {
  display: flex;
  grid-gap: 20px;
}
#footer .footer-brand .footer-socials a {
  display: flex;
  align-items: center;
}
#footer .footer-widgets {
  padding: 70px 0;
  border-top: 1px solid #364067;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr 2fr;
  grid-gap: 30px;
}
#footer .footer-widgets .footer-widget .widget-title {
  color: #fff;
  margin-bottom: 20px;
}
#footer .footer-widgets .footer-widget ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
}
#footer .footer-widgets .footer-widget ul a {
  color: #BDBDBD;
  text-decoration: none;
  line-height: 1.7;
  font-weight: 600;
}
#footer .footer-widgets .footer-widget ul a:hover {
  text-decoration: underline;
}
#footer .footer-widgets .footer-widget .subscribe-form {
  position: relative;
}
#footer .footer-widgets .footer-widget .subscribe-form input {
  margin: 0;
  border: 1px solid #E6E6E6;
}
#footer .footer-widgets .footer-widget .subscribe-form .btn {
  padding: 15px 22px;
  position: absolute;
  right: 0;
  line-height: 1;
  height: 100%;
  border-radius: 0 5px 5px 0;
  border: 1px solid #E6E6E6;
}
#footer .copyright-block {
  padding: 25px;
  color: #fff;
  background-color: #16697A;
}

.form-wrap {
  padding: 40px;
  background-color: #fff;
  border-radius: 10px;
  max-width: 328px;
  width: 100%;
}
.form-wrap h3 {
  margin-bottom: 40px;
}
.form-wrap .form-item-wrap {
  margin-bottom: 5px;
}
.form-wrap .form-item-wrap.no-valid .form-item-field {
  border-color: red;
}
.form-wrap .btn {
  margin-top: 40px;
  width: 100%;
}

.section-hero {
  min-height: 883px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 104px;
}
.section-hero .hero-wrap {
  padding: 50px 0;
  display: flex;
  justify-content: space-between;
  grid-gap: 27px;
}
.section-hero .hero-wrap .hero-content-wrap {
  max-width: 600px;
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 35px;
}
.section-hero .hero-wrap .hero-content-wrap h4 {
  color: #737373;
  font-weight: 500;
}
.section-hero .hero-wrap .hero-content-wrap .buttons-wrap {
  display: flex;
  grid-gap: 10px;
}

.section-why-us {
  padding: 80px 0;
}
.section-why-us .why-us-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin-top: 80px;
}
.section-why-us .why-us-cards .why-us-card {
  border-radius: 10px;
  background: #D9EDFF;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-head {
  padding: 30px;
  display: flex;
  grid-gap: 15px;
  align-items: center;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-head .why-us-card-icon-wrap {
  width: 72px;
  height: 72px;
  background-color: #fff;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-head .why-us-card-icon-wrap img {
  max-width: 32px;
  max-height: 32px;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-body {
  border-radius: 11px;
  border: 1px solid #A9D6FF;
  background: #fff;
  padding: 30px 50px;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-body ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-body ul li {
  line-height: 1.7;
  margin-bottom: 5px;
  font-weight: 600;
  color: #737373;
}
.section-why-us .why-us-cards .why-us-card .why-us-card-body ul li:before {
  content: "- ";
}

.section-video {
  padding: 80px 0;
  background-color: #FAFAFA;
  position: relative;
}
.section-video:before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-color: #16697A;
  z-index: 0;
}
.section-video:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1;
}
.section-video .container {
  position: relative;
  z-index: 5;
}
.section-video .video-wrap {
  aspect-ratio: 8/4;
  margin: 50px 86px 0;
}
.section-video .video-wrap iframe {
  width: 100%;
  height: 100%;
}
.section-video .video-wrap video {
  width: 100%;
}

.section-testimonials {
  padding: 80px 0;
  background-color: #FAFAFA;
}
.section-testimonials .testimonials-cards {
  margin-top: 80px;
}
.section-testimonials .testimonials-cards .testimonial-card {
  border-radius: 5px;
  border: 1px solid #DEDEDE;
  background-color: #fff;
  padding: 30px 35px;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
}
.section-testimonials .testimonials-cards .testimonial-card .testimonial-card-footer {
  display: flex;
  align-items: center;
  grid-gap: 15px;
}
.section-testimonials .testimonials-cards .testimonial-card .testimonial-card-footer .testimonial-card-icon-wrap img {
  display: block;
  border-radius: 50%;
  max-width: 50px;
  aspect-ratio: 1/1;
  object-fit: cover;
}

.section-team {
  padding: 80px 0;
}
.section-team .team-cards {
  margin-top: 80px;
}
.section-team .team-cards .team-card {
  border: 1px solid #eee;
}
.section-team .team-cards .team-card .team-card-body {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  padding: 30px;
  text-align: center;
}
.section-team .team-cards .team-card .team-card-body h6 {
  color: #737373;
  line-height: 1.7;
}
.section-team .team-cards .team-card .team-card-body .team-card-socials {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 15px;
}

.section-contact {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding: 80px 0;
}
.section-contact .form-wrap {
  max-width: 600px;
  background: transparent;
}
.section-contact .form-wrap form {
  margin-top: 40px;
}
.section-contact .form-wrap .grid-wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  margin-bottom: 20px;
}
.section-contact .form-wrap .grid-wrap .form-item-wrap,
.section-contact .form-wrap .grid-wrap .form-item-field {
  margin: 0;
}
.section-contact .form-wrap .grid-wrap .error-msg {
  margin-top: 5px;
  display: inline-block;
}

.section-cta {
  padding: 40px 0;
  background-color: #FAFAFA;
}
.section-cta .cta-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.section-cta .cta-wrap p {
  margin: 10px 0 0;
}